<template>
  <div class="widget-body">
    <loading v-if="!loaded"/>
    <div class="row" v-else>
      <div class="col">
        <vuestic-widget :headerText="`${team.name} / ${team.position}`">

          <div class="row show-buttons-area">
            <router-link
              :to="{ name: 'teams' }"
              class="btn btn-info btn-micro"
            >
              {{'menu.teams.teams' | translate}}
            </router-link>
            <router-link 
              v-if="id"              
              class="btn btn-primary btn-micro"
              :to="{ name: 'update-team', params: { id: this.id } }"
            >
              {{'buttons.update' | translate}}
            </router-link>
          </div>

          <div class="row">
            <div class="col text-center" v-if="team.image">
              <img :src="team.image.data.url.main" />
            </div>
            <div v-else></div>
            <div class="col vue-misc well">{{ team.degrees }}</div>
          </div>

          

        </vuestic-widget>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'

  Vue.component('loading', Loading)

  export default {
    name: 'team',
    data () {
      return {
        team: {},
        name: '',
        position: '',
        degrees: '',
        sort: 0,

        loaded: false
      }
    },
    created () {
      api.get(`teams/${this.$route.params.id}`).then(response => {
        const res = response.data

        this.team = res
        this.id = res.id
        this.name = res.name
        this.position = res.position
        this.degrees = res.degrees
        this.sort = res.sort,

        this.loaded = true
      }).catch(error => {
        this.$store.dispatch('addToastMessage',
          { text: Vue.i18n.translate(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'teams' })
      })
    }
  }
</script>

